@keyframes move-background {
    from {
          -webkit-transform: translate3d(0px, 0px, 0px);
      }
      to { 
          -webkit-transform: translate3d(1000px, 0px, 0px);
      }
  }
  @-webkit-keyframes move-background {
    from {
          -webkit-transform: translate3d(0px, 0px, 0px);
      }
      to { 
          -webkit-transform: translate3d(1000px, 0px, 0px);
      }
  }
  
      @-webkit-keyframes move-background {
      from {
          -webkit-transform: translate3d(0px, 0px, 0px);
      }
      to { 
          -webkit-transform: translate3d(1000px, 0px, 0px);
      }
  }
  
  .background-container{
      position: fixed;
      top: 0;
      left:0;
      bottom: 0;
      right: 0;
  }
  
  .stars {
   background: black url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1231630/stars.png) repeat;
   position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: block;
        z-index: 0;
  }
  
  .twinkling{
      width:10000px;
      height: 100%;
      background: transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/1231630/twinkling.png") repeat;
      background-size: 1000px 1000px;
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 2;
    -webkit-animation:move-background 70s linear infinite;
    animation:move-background 70s linear infinite;
      
  }
  
  .clouds{
      width:10000px;
      height: 100%;
      background: transparent url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/1231630/clouds_repeat.png") repeat;
      background-size: 1000px 1000px;
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 3;
    -webkit-animation:move-background 150s linear infinite;
    animation:move-background 150s linear infinite;
  }
  img{
    height: 70vh;
    width:70vh;
    position: absolute;
    z-index: 3;
    right: 20px;
  }
 .z-index{
    position: absolute;
    z-index: -1;
 }  
.description{
    position: absolute;
    color: white;
    font-size: 30px;
    top: 200px;
    padding: 50px;
    font-family: 'Josefin Sans', sans-serif;
}

:root {
  --color-1: #b284be;
  --color-2: #3e385a;
  --color-3: #016d60;
  --color-4: #01a4a5;
  --color-5: #b284be;
}

span {
  font-family: "Exo", sans-serif;
  font-weight: 700;
  font-size: 40px;
  width: -webkit-min-content;
  width: min-content;
  text-transform: uppercase;
  background: linear-gradient(219deg, 
    #b284be 19%, 
    transparent 19%,transparent 20%, 
    #3e385a 20%, #3e385a  39%,
    transparent 39%,transparent 40%, 
    #016d60 40%,#016d60 59% ,
    transparent 59%,transparent 60%, 
    #01a4a5 60%, #01a4a5 79%,
    transparent 79%, transparent 80%, 
    #b284be 80%);
  background: linear-gradient(219deg, 
    var(--color-1) 19%, 
    transparent 19%,transparent 20%, 
    var(--color-2) 20%, var(--color-2)  39%,
    transparent 39%,transparent 40%, 
    var(--color-3) 40%,var(--color-3) 59% ,
    transparent 59%,transparent 60%, 
    var(--color-4) 60%, var(--color-4) 79%,
    transparent 79%, transparent 80%, 
    var(--color-5) 80%);
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
}
.button{
  cursor: pointer;
  margin: 50px;
 background-color: transparent;
 padding: 15px 30px;
 border-radius: 30px;
 border: none;
 font-size: 20px;
 outline: 6px solid white; 
}
.button:hover{
  outline: 6px solid #3e385a;
  transition: 0.3s;
}
.button a{
  color: white;
  text-decoration: none;
  font-weight: 900;
}



@media screen and (max-width: 768px) {
 .description{
  font-size: 20px;
 }
  span {
    font-size: 30px;
  }
  .button{
    font-size: 16px;
  }
}


 .project_card{
    border-color: #991101 #C23210;
    border-radius: 25px 100px 25px 100px;
    border-style: dashed;
    border-width: 6px;
    padding: 15px;
    margin: 25px;

 }
 .project_card:hover{
    border-color: white;
    transition: 0.3s;
 }
.project_card-title{
   font-size: 22px;
   color: gold;
}
.project_card-description{
    padding-left: 15px;
    margin: 10px 0;
    font-size: 18px;
    color: #88C0C0;
    
}
.project_card-techstacks{
    margin: 20px 0;
    padding-left: 65px;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    color: gold;
    font-size: 18px;
    
}
.rocket{
   padding-top:15px;
}
.project_card-stacklists{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    color: #88C0C0;
   
}
.codes_url,.demo_url{
    position: absolute;
    text-decoration: none;
    color:  #991101 ;
    font-weight: 600;
    cursor: pointer;
    display: flex;
    
    background:gold;
    padding: 15px;
    border-radius: 10px 25px 10px 25px;
    outline: 5px solid #88C0C0; 
}
.codes_url{
   right:250px;
}
.demo_url{
   right: 150px;
}
.codes_url:hover,.demo_url:hover{
    color: white;
    transition: 0.3s;
}
